import React from "react";

const experienceContent = [
  {
    year: "   2011 - 2023",
    position: "Plastic Welding Technician",
    compnayName: "GB Lining Services Ltd",
    details: ` During my dedicated tenure at GB Lining Services LTD spanning from late 2011 to mid-2023, I embarked on a transformative professional journey that showcased both my adaptability and leadership skills. Beginning as a diligent laborer, I swiftly ascended the ranks to become a proficient plastic welding technician, mastering the intricacies of gas membrane installations. My commitment and pursuit of excellence were further underscored by the attainment of my NVQ Level 2 in gas membrane. As my expertise evolved, so did my responsibilities, culminating in the role of project lead, where I took charge of overseeing jobs and leading my own team of skilled individuals. This period marked a pivotal chapter for GB Lining Services, as our collective efforts positioned the company as one of the premier gas membrane installers in the country, earning the prestigious approval of industry leader Visqueen. This achievement stands as a testament to our unwavering dedication to excellence and proficiency in the field.`,
  },
  {
    year: "2010 - 2011",
    position: "Salesman/Repairman",
    compnayName: "Advanced Elite PC Solutions",
    details: `During my dynamic year and a half tenure at Advanced Elite PC Solutions in Braintree town, I navigated a transformative path from a part-time commitment to a full-time role as a skilled computer technician. In addition to repairing computers, Apple Macs, gaming consoles, and phones, I actively engaged in unlocking phones using specialized code software. This journey also saw me progressing from two days a week to handling a full workload. Beyond technical roles, I excelled in computer sales and adeptly managed telephone calls, showcasing my versatility and commitment to delivering comprehensive service in the ever-evolving technology landscape.`,
  },
  {
    year: "2009 - 2010",
    position: "a Marketeer",
    compnayName: "Phone Market",
    details: `During my tenure at Pistea Market in the phone market, I embarked on a journey that not only involved selling phone parts but also delved into the intricate process of unlocking mobile phones for compatibility with various networks. This experience served as my initial exposure to the world of coding, igniting a passion for technology and problem-solving. Through unlocking mobile phones, I gained a profound understanding of the underlying code and algorithms, laying the groundwork for my foray into the realm of programming. It was here that I realized the transformative power of coding and its potential to revolutionize the way we interact with technology. My time at Pistea Market was instrumental in shaping my enthusiasm for coding and technology, ultimately paving the way for my career in this dynamic and innovative field.`,
  },
];

const Experience = () => {
  return (
    <ul>
      {experienceContent.map((val, i) => (
        <li key={i}>
          <div className="icon">
            <i className="fa fa-briefcase"></i>
          </div>
          <span className="time open-sans-font text-uppercase">{val.year}</span>
          <h5 className="poppins-font text-uppercase">
            {val.position}
            <span className="place open-sans-font">{val.compnayName}</span>
          </h5>
          <p className="open-sans-font">{val.details}</p>
        </li>
      ))}
    </ul>
  );
};

export default Experience;
