import React from "react";

const educationContent = [
  {
    year: "2013",
    degree: "NVQ Level 2 in Gas Membrane installation",
    institute: "NeilSalvage.co.uk",
    details: `Completing my NVQ Level 2 in gas membrane installation with Neil Salvage during my employment at GB Lining Services Ltd was a pivotal moment in my career. Under Neil's guidance, I gained invaluable hands-on experience and knowledge in gas membrane installation, further honing my skills in this specialized field. The practical training and theoretical insights provided a solid foundation for my professional growth, equipping me with the expertise to carry out gas membrane installations with precision and confidence. The experience not only enhanced my technical proficiency but also instilled in me a deep sense of accomplishment and dedication to delivering high-quality work in the construction industry. Working with Neil Salvage and GB Lining Services Ltd was instrumental in shaping my career trajectory and solidifying my passion for excellence in gas membrane installation.`,
  },
  {
    year: "2009",
    degree: "Level 1 in Maths and English",
    institute: "Braintree institute course",
    details: `After completing my formal education, I embarked on a journey to enhance my academic proficiency. Recognizing the pivotal role of mathematics and English proficiency, I dedicated myself to a comprehensive Level 1 course in both subjects. This deliberate effort was aimed not only at boosting my grades but also at deepening my understanding of these fundamental disciplines. The commitment to continuous learning reflects my unwavering dedication to personal and intellectual growth, underscoring my proactive approach to self-improvement beyond the confines of traditional education. This pursuit of knowledge aligns seamlessly with my commitment to excellence, both academically and in the broader spectrum of my professional endeavors.`,
  },
  {
    year: "2009",
    degree: "GCSE Results",
    institute: "Notley High School",
    details: `In 2009, I graduated from high school with grades that exceeded my expectations. It was a significant achievement for me, as I had dedicated myself to my studies and worked diligently to excel academically. The sense of accomplishment and pride I felt on graduation day was truly unparalleled. The experience taught me the value of perseverance and hard work, and it provided me with a strong foundation for my future endeavors. The higher than expected grades served as a testament to my determination and commitment to academic excellence, and it was a moment that I will always cherish as a significant milestone in my life.`,
  },
];

const Education = () => {
  return (
    <ul>
      {educationContent.map((val, i) => (
        <li key={i}>
          <div className="icon">
            <i className="fa fa-briefcase"></i>
          </div>
          <span className="time open-sans-font text-uppercase">{val.year}</span>
          <h5 className="poppins-font text-uppercase">
            {val.degree}
            <span className="place open-sans-font">{val.institute}</span>
          </h5>
          <p className="open-sans-font">{val.details}</p>
        </li>
      ))}
    </ul>
  );
};

export default Education;
