import React from "react";

const personalInfoContent = [
  { meta: "first name", metaInfo: "Andrew" },
  { meta: "last name", metaInfo: "Roscoe" },
  { meta: "Age", metaInfo: "30 Years" },
  { meta: "Nationality", metaInfo: "English" },
  { meta: "Freelance", metaInfo: "Available" },
  { meta: "Address", metaInfo: "Braintree" },
  { meta: "phone", metaInfo: "+447794055126" },
  { meta: "Email", metaInfo: "a.roscoe@me.com" },
  { meta: "GitHub", metaInfo: "ARoscoe93" },
  { meta: "langages", metaInfo: "English" },
];

const PersonalInfo = () => {
  return (
    <ul className="about-list list-unstyled open-sans-font">
      {personalInfoContent.map((val, i) => (
        <li key={i}>
          <span className="title">{val.meta}: </span>
          <span className="value d-block d-sm-inline-block d-lg-block d-xl-inline-block">
            {val.metaInfo}
          </span>
        </li>
      ))}
    </ul>
  );
};

export default PersonalInfo;
